import { useEffect, useRef, useImperativeHandle, forwardRef, useState } from 'react'
import { Entity, Scene } from 'aframe-react'

interface ARSceneAFrameProps {
  className?: string
  onSceneReady?: () => void
  cameraFacing?: 'environment' | 'user'
}

export interface ARSceneAFrameRef {
  addEntity: (data: any) => string
  removeEntity: (id: string) => void
  getScene: () => any
  captureScreenshot: () => string
  setCameraFacing: (facing: 'environment' | 'user') => void
}

export const ARSceneAFrame = forwardRef<ARSceneAFrameRef, ARSceneAFrameProps>(({
  className = '',
  onSceneReady,
  cameraFacing: _cameraFacing = 'environment'
}, ref) => {
  const sceneRef = useRef<any>(null)
  const [entities, setEntities] = useState<Array<{ id: string; data: any }>>([])
  const [sceneReady, setSceneReady] = useState(false)

  useEffect(() => {
    // Aguardar o A-Frame estar pronto e configurar canvas compartilhado
    const checkAFrame = () => {
      if (typeof window !== 'undefined' && (window as any).AFRAME) {
        // Aguardar a-scene ser criado e configurar canvas compartilhado
        setTimeout(() => {
          const sceneEl = document.querySelector('a-scene') as any
          if (sceneEl && sceneEl.renderer) {
            const aframeCanvas = sceneEl.renderer.domElement as HTMLCanvasElement
            if (aframeCanvas) {
              // Dar o ID do canvas compartilhado ao canvas do A-Frame
              aframeCanvas.id = 'ar-game-canvas'
              aframeCanvas.className = 'ar-game-canvas'
              console.log('A-Frame canvas configurado como canvas compartilhado')
            }
          }
        }, 500)

        setSceneReady(true)
        if (onSceneReady) {
          onSceneReady()
        }

        requestAnimationFrame(() => {
          const vrButton = document.querySelector('.a-enter-vr-button') as HTMLElement | null
          if (vrButton) {
            vrButton.style.display = 'none'
            vrButton.style.visibility = 'hidden'
            vrButton.style.pointerEvents = 'none'
          }
        })
      } else {
        // Se não estiver pronto, tentar novamente
        setTimeout(checkAFrame, 100)
      }
    }

    checkAFrame()
  }, [onSceneReady])

  useImperativeHandle(ref, () => ({
    addEntity: (data: any) => {
      const id = `entity-${Date.now()}`
      setEntities(prev => [...prev, { id, data }])
      return id
    },
    removeEntity: (id: string) => {
      setEntities(prev => prev.filter(e => e.id !== id))
    },
    getScene: () => {
      if (typeof window !== 'undefined' && (window as any).AFRAME) {
        const sceneEl = document.querySelector('a-scene')
        return sceneEl || sceneRef.current
      }
      return sceneRef.current
    },
    captureScreenshot: () => {
      if (typeof window !== 'undefined' && (window as any).AFRAME) {
        const sceneEl = document.querySelector('a-scene')
        if (sceneEl) {
          const renderer = (sceneEl as any).renderer
          if (renderer && renderer.domElement) {
            return renderer.domElement.toDataURL('image/png')
          }
        }
      }
      return ''
    },
    setCameraFacing: (facing: 'environment' | 'user') => {
      // A câmera é gerenciada pelo ARScreen, apenas aceitar a mudança
      console.log('Camera facing changed to:', facing)
    }
  }), [])

  const sceneProps: any = {
    embedded: true,
    'vr-mode-ui': 'enabled: false',
    'device-orientation-permission-ui': 'enabled: true',
    renderer: 'alpha: true; antialias: true; colorManagement: true; physicallyCorrectLights: false; sortObjects: true; logarithmicDepthBuffer: false; precision: mediump;',
    webxr: 'enabled: false',
    background: 'transparent: true',
    'stats': 'false',
    id: 'ar-scene-main'
  }

  return (
    <div className={`ar-scene-aframe ${className}`} style={{ width: '100%', height: '100%', position: 'relative', zIndex: 1 }}>
      {sceneReady && (
        <Scene {...sceneProps}>
          {/* Assets for textures */}
          <Entity primitive="a-assets">
            <img id="skyTexture" src="/teste-aframe-ar/assets/images/SKY.jpg" crossOrigin="anonymous" />
          </Entity>

          {/* Skybox using the SKY.png texture with transparency */}
          <Entity
            primitive="a-sky"
            rotation="0 -90 0"
            material={{ src: '#skyTexture', opacity: 0.5, transparent: true }}
          />
          <Entity
            id="camera"
            camera={{ active: true }}
            lookControls={{
              enabled: true,
              reverseMouseDrag: false,
              touchEnabled: true,
              magicWindowTrackingEnabled: true,
              pointerLockEnabled: false,
              touchSensitivity: 1,
              mouseSensitivity: 1
            }}
            wasdControls={{
              enabled: false
            }}
            position="0 1.6 0"
          />

          <Entity light={{ type: 'ambient', color: '#888' }} />

          <Entity
            light={{
              type: 'directional',
              color: '#fff',
              intensity: 0.6
            }}
            position="1 1 1"
          />

          {entities.map(entity => (
            <Entity
              key={entity.id}
              {...entity.data}
            />
          ))}

          {/* Cube that follows head rotation */}
          <Entity
            id="head-cube"
            geometry={{ primitive: 'box', width: 0.3, height: 0.3, depth: 0.3 }}
            material={{ color: '#FF6B6B', opacity: 0.9, transparent: true }}
            position="0 1 -1.2"
            rotation="0 0 0"
          />

          {/* Example cube can remain commented or removed if not needed */}
          {false && (
            <Entity
              id="default-cube"
              geometry={{ primitive: 'box', width: 0.6, height: 0.6, depth: 0.6 }}
              material={{ color: '#4CC3D9' }}
              position="0 1 -1.5"
              animation={{
                property: 'rotation',
                to: '0 360 0',
                loop: true,
                dur: 8000
              }}
            />
          )}
        </Scene>
      )}
      {!sceneReady && (
        <div style={{
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'center',
          height: '100%',
          color: '#fff'
        }}>
          Carregando A-Frame...
        </div>
      )}
    </div>
  )
})

ARSceneAFrame.displayName = 'ARSceneAFrame'

